import { Pegawai_posisi } from 'src/app/sdk/hr/models/pegawai-_posisi'
import { Pegawai, Lisensi_tipe, Pegawai_lisensi, Departemen, Divisi } from 'src/app/sdk/hr/models'
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services/direktori-pegawai.service'
import { DirektoriLisensiTipeService, DirektoriPegawaiLisensiService, DirektoriPegawaiPosisiService, DirektoriVPegawaiPosisiService, } from 'src/app/sdk/hr/services'
import { ActivatedRoute, Router } from '@angular/router'
import { Component, OnInit } from '@angular/core'
import { NgxSpinnerService } from 'ngx-spinner'
import { V_pegawai_posisi } from 'src/app/sdk/hr/models/v_pegawai-_posisi'

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private direktoriLisensiTipeService: DirektoriLisensiTipeService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private direktoriVPegawaiPosisiService: DirektoriVPegawaiPosisiService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }

    id_pegawai_lisensi

    pegawai_lisensi: Pegawai_lisensi = {}
    pegawai: Pegawai = {}
    lisensi_tipe: Lisensi_tipe = {}
    pegawai_posisi: V_pegawai_posisi = {}
    ngOnInit() {
        this.id_pegawai_lisensi = this.routerActive.snapshot.paramMap.get('id_pegawai_lisensi')
        console.log(this.id_pegawai_lisensi)
        this.getPegawaiLisensi();
    }
    getPegawaiLisensi() {
        this.spinner.show()
        this.direktoriPegawaiLisensiService.getDirektoriPegawaiLisensisId(this.id_pegawai_lisensi).subscribe(
            response => {
                this.pegawai_lisensi = response.data
                this.getPegawai(response.data.id_pegawai)
                this.getPegawaiPosisi(response.data.id_pegawai)
                this.getLisensiTipe(response.data.id_lisensi_tipe)
                this.spinner.hide()
            }
        )

    }
    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data
            }
        )
    }

    getPegawaiPosisi(id_pegawai: string) {
        this.direktoriVPegawaiPosisiService.getDirektoriVPegawaiPosisisId(id_pegawai).subscribe(
            response => {
                this.pegawai_posisi = response.data
            }
        )
    }
    getLisensiTipe(id_lisensi_tipe: string) {
        this.direktoriLisensiTipeService.getDirektoriLisensiTipesId(id_lisensi_tipe).subscribe(
            response => {
                this.lisensi_tipe = response.data
            }
        )
    }
    goToBack() {

        this.router.navigate(['/hr/direktori/development/lisensi/list'])
    }

}
