import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { DirektoriPegawaiLisensiService, DirektoriLisensiTipeService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Pegawai_lisensi, Lisensi_tipe } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private direktoriLisensiTipeService: DirektoriLisensiTipeService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Pegawai_lisensi = {}
    ngOnInit() {
        this.form()
        this.getPreeData()

    }



    listTipeLisensi: Lisensi_tipe[] = []
    getPreeData() {
        this.direktoriLisensiTipeService.getDirektoriLisensiTipes().subscribe(
            data => {
                this.listTipeLisensi = data.data
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pegawai_lisensi>
    form() {
        this.dataForm = this.fb.group<Pegawai_lisensi>(
            {
                id_lisensi_tipe: new FormControl('', [Validators.required]),
                id_pegawai_lisensi: new FormControl('', [Validators.required]),
                status_lisensi: new FormControl('', [Validators.required]),
                id_pegawai: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                tanggal_berlaku: new FormControl(''),
                nomor_lisensi: new FormControl(''),
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPegawaiLisensiService.postDirektoriPegawaiLisensis().subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/development/lisensi/list'])
    }

}
