import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { CompanyLisensiService, CompanyPerusahaanService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Lisensi, Perusahaan } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { Vendor, Contact } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-edit',
    templateUrl: './edit.component.html',
    styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyLisensiService: CompanyLisensiService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService,
    ) { }

    id_lisensi: string = ''
    data: Lisensi = {};
    toogleUpload = false
    ngOnInit() {
        this.id_lisensi = this.activatedRoute.snapshot.paramMap.get('id_lisensi')
        this.form();
        this.getPreeData()
        this.getData();

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'company_lisensi.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }


    listVendor: Vendor[] = [];
    getPreeData() {
        this.vendorVendorService.getVendorVendors().subscribe(
            data => {
                this.listVendor = data.data;
            }
        )
    }

    listContact: Contact[] = [];
    a = 0
    getContact() {
        this.vendorContactService.getVendorContacts(JSON.stringify({ ref_id_module: this.dataForm.value.id_vendor })).subscribe(
            data => {
                this.listContact = data.data
                if (this.a > 1) {
                    this.dataForm.get('contact').setValue(null)
                }
                this.a++;
                console.log(this.a)
            }
        )
    }


    getData() {
        this.spinner.show()
        this.companyLisensiService.getCompanyLisensisId(this.id_lisensi).subscribe(
            data => {
                this.dataForm.setValue(data.data)
                this.spinner.hide()
                this.getContact()
                this.singleUploadService.param.next({
                    for_module: "company_lisensi",
                    for_table: "erp.lisensi",
                    for_module_id: data.data.id_lisensi
                })
            }
        )
    }
    dataForm: FormGroupTypeSafe<Lisensi>;
    form() {
        this.dataForm = this.fb.group<Lisensi>(
            {
                nomor_lisensi: new FormControl('', [Validators.required]),
                tanggal_berlaku: new FormControl('', [Validators.required]),
                status_lisensi: new FormControl(''),
                tentang_lisensi: new FormControl(''),
                id_perusahaan: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                id_dokumen: new FormControl(''),
                id_user_create: new FormControl(''),
                id_vendor: new FormControl(''),
                contact: new FormControl(''),
                id_lisensi: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                kode_lisensi: new FormControl(''),
                nama_lisensi: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyLisensiService.putCompanyLisensisId({ id: this.id_lisensi, body: this.data }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/lisensi/list'])
    }


}
