import { Component, OnInit } from '@angular/core'
import { FilterFuctionService } from 'src/app/services/filter-fuction.service'
import { NzMessageService } from 'ng-zorro-antd'
import { NgxSpinnerService } from 'ngx-spinner'
import { DirektoriPegawaiLisensiService } from 'src/app/sdk/hr/services/direktori-pegawai-lisensi.service'
import { Pegawai_lisensi } from 'src/app/sdk/hr/models'
import { DirektoriLisensiTipeService } from 'src/app/sdk/hr/services'

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    pageIndex = 1
    pageSize = 10
    total = 1
    listOfData = []
    loading = true
    sortValue: string | null = null
    sortKey: string | null = null
    search: string = ''

    ngOnInit() {
        this.searchData()
    }
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key
        this.sortValue = sort.value
        this.searchData()
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1
        }
        this.loading = true
        this.direktoriPegawaiLisensiService
            .getDirektoriPegawaiLisensis(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey, sortValue: this.sortValue, search: this.search
            }))
            .subscribe((data: any) => {
                this.loading = false
                this.total = data.data.total
                this.listOfData = data.data.data
                console.log(data.data.data)
            })
    }

    updateFilter(value: string): void {
        this.search = value
        this.searchData(true)
    }




    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiLisensiService.deleteDirektoriPegawaiLisensisId(id).subscribe(
            data => {
                this.searchData()
                this.spinner.hide()
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

}
