import { Component, OnInit } from '@angular/core';
import { CompanyLisensiService, CompanyPerusahaanService } from 'src/app/sdk/hr/services';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { Perusahaan, Lisensi } from 'src/app/sdk/hr/models';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { Vendor, Contact } from 'src/app/sdk/asset/models';


@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private companyLisensiService: CompanyLisensiService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router
    ) { }

    data: Lisensi = {};
    ngOnInit() {
        this.form();
        this.getPreeData()
    }


    listVendor: Vendor[] = [];
    getPreeData() {
        this.vendorVendorService.getVendorVendors().subscribe(
            data => {
                this.listVendor = data.data;
            }
        )
    }

    listContact: Contact[] = [];
    getContact() {
        this.vendorContactService.getVendorContacts(JSON.stringify({ ref_id_module: this.dataForm.value.id_vendor })).subscribe(
            data => {
                this.listContact = data.data
                this.dataForm.get('contact').setValue(null)
                console.log(this.dataForm.value.contact)
            }
        )
    }

    dataForm: FormGroupTypeSafe<Lisensi>;
    form() {
        this.dataForm = this.fb.group<Lisensi>(
            {
                nomor_lisensi: new FormControl('', [Validators.required]),
                tanggal_berlaku: new FormControl('', [Validators.required]),
                tentang_lisensi: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                status_lisensi: new FormControl(''),
                kode_lisensi: new FormControl(''),
                id_vendor: new FormControl(''),
                contact: new FormControl(''),
                nama_lisensi: new FormControl(''),

            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.data = this.dataForm.value;
        this.companyLisensiService.postCompanyLisensis(this.data).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/company/module/lisensi/list'])
    }

}
