import { Pegawai_lisensi, Lisensi_tipe } from 'src/app/sdk/hr/models'
import { DirektoriLisensiTipeService, DirektoriPegawaiLisensiService } from 'src/app/sdk/hr/services'
import { Component, OnInit } from '@angular/core'
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper'
import { PesanService } from 'src/app/services/tool/pesan.service'
import { NgxSpinnerService } from 'ngx-spinner'
import { Router, ActivatedRoute } from '@angular/router'
import { FormControl, Validators } from '@angular/forms'
import { SingleUploadService } from 'src/app/components/document/single-upload.service'

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private direktoriPegawaiLisensiService: DirektoriPegawaiLisensiService,
        private direktoriLisensiTipeService: DirektoriLisensiTipeService,
        private singleUploadService: SingleUploadService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute
    ) { }

    data: Pegawai_lisensi = {}
    toogleUpload = false;
    ngOnInit() {
        this.id = this.activatedRoute.snapshot.paramMap.get('id')
        this.form()
        this.getPreeData()
        this.getData()
        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'hr_lisensi.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    id: string = ''
    getData() {
        this.spinner.show()
        this.direktoriPegawaiLisensiService.getDirektoriPegawaiLisensisId(this.id).subscribe(
            data => {
                this.dataForm.setValue(data.data)

                this.singleUploadService.param.next({
                    for_module: "hr_lisensi",
                    for_table: "erp_hr.lisensi",
                    for_module_id: data.data.id_pegawai_lisensi
                })
            }
        )
    }


    listTipeLisensi: Lisensi_tipe[] = []
    getPreeData() {
        this.direktoriLisensiTipeService.getDirektoriLisensiTipes().subscribe(
            data => {
                this.listTipeLisensi = data.data
                this.spinner.hide()
            }
        )
    }


    dataForm: FormGroupTypeSafe<Pegawai_lisensi>
    form() {
        this.dataForm = this.fb.group<Pegawai_lisensi>(
            {
                id_lisensi_tipe: new FormControl('', [Validators.required]),
                id_pegawai_lisensi: new FormControl('', [Validators.required]),
                status_lisensi: new FormControl('', [Validators.required]),
                id_pegawai: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
                id_user_create: new FormControl(''),
                tanggal_berakhir: new FormControl(''),
                tanggal_berlaku: new FormControl(''),
                nomor_lisensi: new FormControl(''),
                id_dokumen: new FormControl('')
            }
        )
    }

    simpan() {
        this.spinner.show()


        this.data = this.dataForm.value
        this.direktoriPegawaiLisensiService.putDirektoriPegawaiLisensisId({
            id: this.id,
            body: this.data
        }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan')
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/direktori/development/lisensi/list'])
    }

}
