import { Component, OnInit } from '@angular/core';
import { Lisensi } from 'src/app/sdk/hr/models';
import { CompanyLisensiService } from 'src/app/sdk/hr/services';
import { VendorVendorService, VendorContactService } from 'src/app/sdk/asset/services';
import { Vendor, Contact } from 'src/app/sdk/asset/models';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private routerActive: ActivatedRoute,
        private companyLisensiService: CompanyLisensiService,
        private vendorVendorService: VendorVendorService,
        private vendorContactService: VendorContactService,
        private spinner: NgxSpinnerService,
        private router: Router,
    ) { }



    dataLisensi: Lisensi = {}
    dataVendor: Vendor = {}


    id_lisensi: string = ''

    ngOnInit() {
        this.id_lisensi = this.routerActive.snapshot.paramMap.get('id')
        this.getLisensi();


    }

    getLisensi() {
        this.spinner.show()
        this.companyLisensiService.getCompanyLisensisId(this.id_lisensi).subscribe(
            response => {
                this.dataLisensi = response.data;
                this.getVendor(this.dataLisensi.id_vendor)
                this.getContact(this.dataLisensi.contact)
                this.spinner.hide()
            }
        )
    }

    dataContact: Contact = {}
    getContact(contact) {
        this.vendorContactService.getVendorContactsId(contact).subscribe(
            data=> {
                this.dataContact = data.data
            }
        )
    }

    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }

    goToBack() {

        this.router.navigate(['/hr/company/module/lisensi/list'])
    }

}
