import { Lisensi } from 'src/app/sdk/hr/models/lisensi';
import { CompanyLisensiService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { FilterFuctionService } from 'src/app/services/filter-fuction.service';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { Kantor } from 'src/app/sdk/hr/models';

@Component({
    selector: 'app-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {
    constructor(
        private companyLisensiService: CompanyLisensiService,
        private filterFuctionService: FilterFuctionService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    ngOnInit() {
        this.getData()
    }

    sortKey: string | null = '';
    sortValue: string | null = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.getData();
    }
    search: string = "";

    listOfDisplayData: Lisensi[] = []
    listOfData: Lisensi[] = []
    getData() {
        this.spinner.show()
        this.companyLisensiService.getCompanyLisensis(JSON.stringify({sortKey: this.sortKey, sortValue: this.sortValue, search: this.search})).subscribe(
            data => {
                this.listOfDisplayData = data.data;
                this.listOfData = data.data;
                this.spinner.hide()

            }
        )
    }

    reset(): void {
        this.search = '';
        this.getData();
    }

    delete(id) {
        this.spinner.show();
        this.companyLisensiService.deleteCompanyLisensisId(id).subscribe(
            data => {
                this.getData();
                this.spinner.hide();
            }
        )
    }

    batalDelete() {
        this.nzMessageService.info('click cancel');
    }



}
